import React from 'react'
import { NavLink } from 'react-router-dom'
import logo512 from '../../Assets/images/logo512.png'
import styles from './user.module.css'

const Users = (props) => {

    let pagesCount = Math.ceil(props.totalUsersCount / props.pageSize)

    let pages = []
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i)
    };

    let currentPages = []
    for (let i = props.currentPage - 5; i <= props.currentPage + 5; i++) {
        currentPages.push(i)
        console.log(currentPages)
    }

    return (
        <div className={styles.content}>
            <div>
                <button className={styles.buttonsPaggination} onClick={(e) => { props.onPageChanged(pages[0], props.pageSize) }}>{pages[0]}</button>
                
                {props.currentPage != 1 ?
                    <button className={styles.buttonsPaggination}
                        onClick={(e) => {
                            props.onPageChanged(props.currentPage - 1, props.pageSize)
                        }}>{"<<"}</button> : null}
                
                <span className={styles.selectedPage}>{props.currentPage}</span>
                
                {props.currentPage != pages.length ?
                    <button className={styles.buttonsPaggination}
                        onClick={(e) => {
                            props.onPageChanged(props.currentPage + 1, props.pageSize)
                        }}>{">>"}</button> : null}
                        {console.log('pages' + pages.length)}
                
                <button className={styles.buttonsPaggination} onClick={(e) => { props.onPageChanged(pages[pages.length - 1], props.pageSize) }}>{pages[pages.length - 1]}</button>
            </div>
            {props.users.map(u => <div key={u.id}>
                <span>
                    <div>
                        <NavLink to={'/profile/' + u.id}>
                            <img src={u.photos.small != null ? u.photos.small : logo512} className={styles.photo} />
                        </NavLink>
                    </div>
                    <div>
                        {u.followed ?
                            <button disabled={props.followingInProgress.some(id => id === u.id)} onClick={() => {
                                props.unfollow(u.id)
                            }}>unfollow</button> :
                            <button disabled={props.followingInProgress.some(id => id === u.id)} onClick={() => {
                                props.follow(u.id)
                            }}>follow</button>}
                    </div>
                </span>
                <span>
                    <div>{u.name}</div>
                    <div>{u.status}</div>
                </span>
                <span>
                    <div>{"u.location.country"}</div>
                    <div>{"u.location.city"}</div>
                </span>
            </div>)}</div>
    )
}

export default Users;