import { createSelector } from "reselect"

export const getUsers = (state) => {
    return state.usersPage.users
}


//Selector проверяет зависимости getUsers и результат его выполнения users
//если зависимости не изменились то функция не запускается, соотвественно не запускается
//рендер на странице
export const getUsersSelectot = createSelector(
    getUsers, (users) => {
    return users.filter(u => true)
})

export const getPageSize = (state) => {
    return state.usersPage.pageSize
}

export const getTotalUserCount = (state) => {
    return state.usersPage.totalUsersCount
}

export const getCurrentPage = (state) => {
    return state.usersPage.currentPage
}

export const getIsFetching = (state) => {
    return state.usersPage.isFetching
}

export const getFollowingInProgress = (state) => {
    return state.usersPage.followingInProgress
}

export const getIsAuth = (state) => {
    return state.auth.isAuth
}