import React from 'react'
import { connect } from "react-redux";
import {
    follow,
    setCurrentPage,
    setUsers,
    toggleIsFetching,
    unfollow,
    getUsersThunkCreator,
    getUsersThunkCreatorWithCurrentPage
} from '../../redux/users_reducer';
import Users from './Users';
import Preloader from '../common/preloader';
import { changeSubscriptionAPI } from '../../API/api'
import { compose } from 'redux';
import {
    getCurrentPage,
    getIsFetching,
    getPageSize,
    getTotalUserCount,
    getUsers,
    getFollowingInProgress,
    getIsAuth,
    getUsersSelectot
} from '../../redux/users-selectors';

class UsersContainer extends React.Component {

    //вызывается после сборки компоненты
    componentDidMount() {
        this.props.getUsersThunkCreator(this.props.currentPage, this.props.pageSize)
    }

    onPageChanged = (pageNumber, pageSize) => {
        this.props.getUsersThunkCreatorWithCurrentPage(pageNumber, pageSize)
    }

    render() {

        return <>
            {this.props.isFetching ? <Preloader /> : null}
            <Users
                totalUsersCount={this.props.totalUsersCount}
                pageSize={this.props.pageSize}
                currentPage={this.props.currentPage}
                users={this.props.users}
                follow={this.props.follow}
                unfollow={this.props.unfollow}
                onPageChanged={this.onPageChanged}
                changeSubscriptionAPI={changeSubscriptionAPI}
                followingInProgress={this.props.followingInProgress}
                isAuth={this.props.isAuth} />
        </>
    }
}

let mapStateToProps = (state) => {
    return {
        users: getUsersSelectot(state),
        pageSize: getPageSize(state),
        totalUsersCount: getTotalUserCount(state),
        currentPage: getCurrentPage(state),
        isFetching: getIsFetching(state),
        followingInProgress: getFollowingInProgress(state),
        isAuth: getIsAuth(state)
    }
};

// передаем второй параметр через все обработчики первого параметра, начиная с последнего
export default compose(
    //withAuthRedirect,
    connect(mapStateToProps,
        {
            follow,
            unfollow,
            setUsers,
            setCurrentPage,
            toggleIsFetching,
            getUsersThunkCreator,
            getUsersThunkCreatorWithCurrentPage
        }))(UsersContainer)


